.Container {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    grid-template-areas: "main";
    min-height: 100vh;
    background-color: var(--background-white);
}

.Home {
    grid-area: main;
    -ms-flex-item-align: center;
        align-self: center;
    width: min(90vw, 1000px);
    padding-left: max(5vw, 10px);
    padding-right: max(5vw, 10px);
    padding-bottom: 8vh;
}

.Home .logo-container .Array {
    padding: 4vh 0 2vh 0;
    height: 30vh;
}

.Home .text-container {
    text-align: center;
}

.Home .text-container strong {
    font-size: min(8rem, 15vw);
    color: var(--text-dark-gray);
}

.Home .text-container p {
    margin-top: min(1rem, 1vh);
    font-size: min(2rem, 4vw);
    color: var(--text-light-gray);
}

.Home .button-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.Home .button-container .RedirectButton {
    height: min(100px, 15vw);
    width: min(300px, 45vw);
    margin: min(1vw, 20px);
}


