.RedirectButton {
    border: 0;

    background: url('icons/button-gray-3x1.svg') center;
    background-size: 100% 100%;
    background-color: transparent;
    background-repeat: no-repeat;

    font-size: min(3rem, 5vw);
    color: var(--background-white);
    -webkit-transition: 150ms;
    -o-transition: 150ms;
    transition: 150ms;

    text-decoration: none;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.RedirectButton span {
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;
}

.RedirectButton:hover {
    cursor: pointer;
    background: url('icons/button-pressed-orange-3x1.svg') top;
    background-size: 100% 100%;
}

.RedirectButton:hover span {
    position: relative;
    
    bottom: 5%;
    left: 2.5%;
}


